.header {
}
.header--no-js {
}
.header__body {
  padding-top: 12px;
  padding-bottom: 9px;
  display: grid;
  align-items: center;
  justify-items: center;
  column-gap: 10px;

  @media (min-width: $width-tablet+px) {
    grid-template-columns: 1fr 3fr 1fr;
  }
}

.header__logo {
}
.header__menu {
  height: 100%;
}
.header-menu {
}
.header-menu__toggle {
  display: none;
}
.header-menu__list {
}
.site-list {
  height: 100%;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: flex-start;
}
.site-list__item {
  margin-right: 9px;

  &:last-child {
    margin-right: 0;
  }
}
.site-list__link {
  padding: 5px;
  font-size: 16px;
  line-height: 19px;
  color: #4f4f4f;
  cursor: pointer;
}
.header-menu__actions {
  height: 100%;

  @media (min-width: $width-tablet+px) {
    justify-self: end;
  }
}
.header-actions {
}
.header-actions__item {
  height: 100%;
  display: flex;
  align-items: center;
}
.header-actions__link {
  display: flex;
  align-items: center;
  font-weight: bold;
  font-size: 20px;
  line-height: 23px;

  span {
    white-space: nowrap;
    color: #3e0d41;
  }
}
.header-actions__link--tel {
  svg {
    margin-right: 10px;
  }
}
